/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/

:root {
  scroll-behavior: smooth;
}

body,
html {
  font-family: "Poppins";
  overflow: hidden;
  background: #eef3ff;
  min-height: auto !important;
  top: 0 !important;
}

p {
  font-weight: 400;
}

a {
  color: #da3c3b;
  text-decoration: none !important;
  transition: 0.5s all;
}

a:hover {
  color: #000000;
  text-decoration: none;
}

html,
body {
  scroll-behavior: smooth;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins" !important;
}
h2 {
  font-weight: bolder;
}

