/*--------------------------------------------------------------
# Sidebar css 
--------------------------------------------------------------*/

.admin_main {
  margin-left: 280px;
  transition: 0.1s all;
}

.admin_full {
  margin: 0;
}

.admin_header {
  height: 70px;
  background-color: #144881;
  display: flex;
  align-items: center;
  padding: 0 20px;
}

.sidebar_btn {
  font-size: 18px;
  color: #fff !important;
}

.siderbar_section {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9;
  width: 280px;
  height: 100vh;
  background-image: url(../img/verification_bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 89%;
  border-right: 10px solid #144881;
  transition: 0.1s all;
}
.vaccine_category {
  margin: 0 20px;
}
.hide_sidebar {
  width: 0;
  left: -100%;
}

.siderbar_section::after {
  position: absolute;
  top: 0;
  content: "";
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
  opacity: 0.7;
}

.siderbar_inner {
  position: relative;
  z-index: 1;
}

.sidebar_logo a {
  display: inline-flex;
  align-items: center;
  background-color: #fff;
  border-radius: 10px;
  padding: 10px 10px;
}

.sidebar_logo img {
  max-width: 100px;
  margin: 0 auto;
}

.sidebar_logo {
  text-align: center;
  padding: 40px 20px;
}
.sidebar_menus ul li a {
  color: #fff;
  font-weight: 500;
  display: flex;
  font-size: 15px;
  padding: 16px 15px;
  border-radius: 15px 0 0 15px;
  position: relative;
  align-items: flex-start;
}
.sidebar_menus {
  height: calc(100vh - 230px);
  overflow: auto;
}
.sidebar_menus::-webkit-scrollbar {
  display: none;
}
.sidebar_menus ul li a i {
  width: 18px;
  font-size: 16px;
  margin-top: 3px;
  display: flex;
  justify-content: center;
  margin-right: 9px;
}
.filter {
  transform: unset !important;
  top: 30px !important;
  right: -16px !important;
  left: auto !important;
  min-width: 140px;
  text-align: left;
  border: 0 !important;
  border-radius: 2px;
  box-shadow: 0 7px 6px #0000000d;
  padding: 14px 2px;
}
.filter::after {
  position: absolute;
  content: "";
  top: -11px;
  right: 23px;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 12px solid white;
}
.Profile_dropdown .profile {
  transform: unset !important;
  top: 75px !important;
  right: -8px !important;
  left: auto !important;
  min-width: 180px;
  text-align: left;
  border: 0 !important;
  border-radius: 17px;
  box-shadow: 0 7px 6px #0000000d;
  padding: 14px 2px;
}

.profile::after {
  position: absolute;
  content: "";
  top: -11px;
  right: 23px;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 12px solid white;
}

