/*--------------------------------------------------------------
# Login css
--------------------------------------------------------------*/
.container {
  max-width: 1240px;
}
.login_page {
  background-image: url(../img/verification_bg.png);
  height: 100vh;
  background-size: cover;
  background-position: 80px 100%;
  position: relative;
  background-repeat: no-repeat;
}
.login_page::after {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #000;
  opacity: 0.6;
}
.login_page_form {
  height: 100vh;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px 45px;
  position: relative;
  z-index: 1;
  text-align: center;
}
.formheader h1 {
  font-size: 20px;
  font-weight: 700;
  color: #da3c3b;
  margin-bottom: 6px;
}
.formheader p {
  font-size: 14px;
  margin: 0;
}
.form-design .form-control {
  border-radius: 10px;
  height: 50px;
  font-size: 14px;
  padding: 11px 18px;
  box-shadow: unset;
}

.form-design .form-control:focus {
  border: 1px solid #da3c3b;
}
.form-design .form-group label {
  display: block;
  margin: 0 0 8px 0;
  font-size: 14px;
  font-weight: 500;
  text-align: left;
}
.form-design .form-group {
  margin-bottom: 22px;
}
.form-design .for_got {
  font-size: 14px;
  font-weight: 500;
  display: block;
  text-align: left;
}

.comman_btn {
  background-color: #144881 !important;
  color: #fff !important;
  border: 1px dashed #144881 !important;
  transition: 0.3s all;
  font-size: 15px;
  font-weight: 600;
  padding: 12px 34px;
  display: inline-block;
  border-radius: 50px;
}

.comman_btn:hover {
  background-color: #fff !important;
  border-color: #144881 !important  ;
  color: #144881 !important;
}

.comman_btn2 {
  background-color: #da3c3b !important;
  color: #fff !important;
  border: 1px dashed #da3c3b;
  transition: 0.3s all;
  font-size: 15px;
  font-weight: 600;
  padding: 12px 34px;
  display: inline-block;
  border-radius: 50px;
}

.comman_btn2:hover {
  background-color: #fff !important;
  border-color: #da3c3b;
  color: #da3c3b !important;
}

.plan_details_inner textarea.form-control {
  height: 140px;
}

.count_Sec {
  display: block;
  font-weight: 600;
  color: #f20;
  font-size: 16px;
}
.Profile_dropdown button img {
  max-width: 100%;
  border-radius: 50px;
  width: 100%;
  height: 100%;
}
.Profile_dropdown button {
  height: 50px;
  width: 50px;
  border-radius: 50px;
  border: 0 !important;
  background-color: transparent !important;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: unset !important;
  border: 2px solid #fff !important;
  padding: 0 !important;
}

.Profile_dropdown .dropdown-item {
  color: #da3c3b;
  font-weight: 500;
  font-size: 13px;
}
.formheader img {
  max-width: 130px;
  margin-bottom: 35px;
}
.translation-links a img {
  max-width: 22px;
  margin-right: 10px;
}

.translation-links a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  width: 128px;
  margin: 0 8px;
  border-radius: 10px;
  padding: 0;
  color: #da3c3b;
  font-size: 15px;
  font-weight: 500;
  text-transform: capitalize;
  border: 2px solid #ffffff;
  height: 50px;
}

div#google_translate_element {
  display: none;
}

.skiptranslate {
  display: none;
}

.translation-links a:focus {
  background-color: #da3c3b;
  color: #fff;
  border-color: #da3c3b;
}
