}
.design_outter_comman .comman_header .form-design .form-control {
  height: 38px;
  border-radius: 6px;
  padding: 0 17px;
  box-shadow: unset !important;
  border: 0;
  font-size: 13px;
}
.design_outter_comman .comman_header .icons_set i {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 12px;
  color: #144881;
  font-weight: 600;
  cursor: pointer;
}
.check_toggle label::after {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  width: 38px;
  height: 18px;
  background-color: #cacaca;
  border-radius: 10px;
  transition: 0.5s all;
}
.check_toggle label::before {
  position: absolute;
  content: "";
  top: 2px;
  right: 21px;
  width: 14px;
  height: 14px;
  background-color: #ffffff;
  border-radius: 50px;
  z-index: 1;
  transition: 0.5s all;
}
.check_toggle input:checked ~ label::before {
  right: 3px;
  background-color: #fff;
}
.check_toggle label {
  position: relative;
  display: inline-block;
  font-weight: 400;
  color: #fff;
  font-size: 14px;
  padding-right: 27px;
}
.check_toggle {
  width: 100%;
  margin-top: -29px;
  margin-left: 10px;
}
.check_toggle input:checked ~ label::after {
  background-color: #da3c3b;
}
