    border-radius: 50px 0px 50px 50px;
  }
  .time_chat {
    font-size: 11px;
    margin: 6px 15px 0 15px;
  }
  .bottom_chat {
    padding: 14px 10px;
    border-radius: 10px;
  }
  .Send_btns {
    width: 40px;
    height: 40px;
  }
  .Send_btns img {
