  }
  .bottom_chat_inner {
    border-radius: 10px;
    height: 46px;
    padding: 0 8px;
  }
  .attechments_btns img {
    max-width: 22px;
    margin-top: -4px;
  }
  .record_btn img {
    max-width: 21px;
  }
  .bottom_chat_inner .form-control {
    margin: 0 5px !important;
